import moment from "moment";
import React, { useEffect, useState } from 'react';
import Web3 from 'web3';
import { BLOCK_EXPLORER, MAIN_COIN, TOKEN_NAME } from "../contracts/contractInfo";
import MORALIS from "../moralis/moralisinfo";
import { timeSince } from "../utils/contractFunctions";

import { ellipsisAddr } from "../utils/functions";
import closeIcon from '../assets/img/cancel.png';

window.web3 = new Web3(window.ethereum);
console.log(window.web3)

export default function EventBar({ account, contract, showEvents, setShowEvents }) {
  let raw = JSON.stringify({
		"where": {},
		"limit": 200,
		"order": "-createdAt",
		"_method": "GET",
		"_ApplicationId": MORALIS.APP_ID,
		"_ClientVersion": "js3.4.1",
		"_MasterKey": MORALIS.MASTER_KEY
	});

	const requestOptions = {
		method: 'POST',
		headers: {"Content-Type": "application/json"},
		body: raw,
		redirect: 'follow'
	};
  const [recentEvents, setRecentEvents] = useState([]);

  useEffect(async () => {
    // getEvents();
    await getLatestEvents();
    const _eventInterval = setInterval(async()=>{
      await getLatestEvents();
    }, 20 * 1000);
    return ()=>{
      clearInterval(_eventInterval);
    }
  }, [account, contract])

  const getLatestEvents = async()=>{
    if (!contract) return;
    let response = await fetch(`${MORALIS.DAPP_URL}/classes/user_stake`, requestOptions);
    let user_stake_data = await response.json();
    user_stake_data = user_stake_data.results ?? [];

    response = await fetch(`${MORALIS.DAPP_URL}/classes/stake_collect`, requestOptions);
    let stake_collect_data = await response.json();
    stake_collect_data = stake_collect_data.results ?? [];

    response = await fetch(`${MORALIS.DAPP_URL}/classes/stake_sell_request`, requestOptions);
    let stake_sell_request_data = await response.json();
    stake_sell_request_data = stake_sell_request_data.results ?? [];

    response = await fetch(`${MORALIS.DAPP_URL}/classes/user_lobby`, requestOptions);
    let user_lobby_data = await response.json();
    user_lobby_data = user_lobby_data.results ?? [];

    response = await fetch(`${MORALIS.DAPP_URL}/classes/stake_loan_request`, requestOptions);
    let stake_loan_request_data = await response.json();
    stake_loan_request_data = stake_loan_request_data.results ?? [];

    console.log("user_stake_data =========", user_stake_data);
    console.log("user_lobby_data =========", user_lobby_data);
    // moralisRecentEvents = [user_stake_data.results, stake_collect_data.results, user_lobby_data.results, stake_sell_request_data.results, stake_loan_request_data.results];
    let allEvents = [];
    user_stake_data.forEach(el=>{
      allEvents.push({
        type: 4,
        address: el.addr,
        text: `Stake Started: ${parseInt(el.rawAmount) / 1e18} ${TOKEN_NAME} for ${el.duration} days`,
        time: timeSince(parseInt(el.timestamp) * 1000),
        link: `${BLOCK_EXPLORER}/tx/${el.transaction_hash}`,
        timestamp: el.timestamp
      })
    });

    stake_collect_data.forEach(el=>{
      allEvents.push({
        type: 2,
        address: el.addr,
        text: `Stake Collected: ${el.rawAmount / 1e18} ${MAIN_COIN}`,
        time: timeSince(parseInt(el.timestamp) * 1000),
        link: `${BLOCK_EXPLORER}/tx/${el.transaction_hash}`,
        timestamp: el.timestamp
      });
    });

    stake_sell_request_data.forEach(el=>{
      allEvents.push({
        type: 3,
        address: el.addr,
        text: `${el.rawAmount / 1e18} ${TOKEN_NAME} Stake sell offer for ${parseInt(el.price)/1e18} ${MAIN_COIN}`,
        time: timeSince(parseInt(el.timestamp) * 1000),
        link: `${BLOCK_EXPLORER}/tx/${el.transaction_hash}`,
        timestamp: el.timestamp
      });
    });
    
    user_lobby_data.forEach(el=>{
      allEvents.push({
        type: 1,
        address: el.addr,
        text: `Auction Entered: ${el.rawAmount / 1e18} ${MAIN_COIN}`,
        time: timeSince(parseInt(el.timestamp) * 1000),
        link: `${BLOCK_EXPLORER}/tx/${el.transaction_hash}`,
        timestamp: el.timestamp
      })
    });

    stake_loan_request_data.forEach(el=>{
      allEvents.push({
        type: 5,
        address: el.addr,
        text: `${parseInt(el.rawAmount) / 1e18} ${MAIN_COIN} LOAN request for ${parseInt(el.duration)} Days`,
        time: timeSince(parseInt(el.timestamp) * 1000),
        link: `${BLOCK_EXPLORER}/tx/${el.transaction_hash}`,
        timestamp: el.timestamp
      })
    });
    allEvents.sort((a, b)=>{
      return b.timestamp - a.timestamp;
    });
    setRecentEvents(allEvents);
  }

  const getEvents = async () => {
    if (!contract) return;
    let lastEvents = [];
    let blockNumber = await window.web3.eth.getBlockNumber();
    // console.log(blockNumber);
    for (; blockNumber > 21165104; blockNumber = blockNumber - 5001) {
      const fromBlock = (blockNumber - 5000) < 0 ? 0 : (blockNumber - 5000);
      const _events = await contract.getPastEvents('AllEvents', {
        // fromBlock: fromBlock,
        // toBlock: blockNumber,
        fromBlock: fromBlock,
        toBlock: blockNumber,
      });
      _events.reverse();
      // console.log(_events);
      _events.map((_event) => {
        if (lastEvents.length > 19) return;
        if (_event.event === "UserLobby") {
          const _type = 1;
          const _address = _event.returnValues.addr;
          const _text = `Auction Entered: ${_event.returnValues.rawAmount / 1e18} BNB`;
          const _time = moment.unix(_event.returnValues.timestamp).fromNow();
          const _link = `https://testnet.bscscan.com/tx/${_event.transactionHash}`;
          // console.log({address: _address, text: _text, time: _time, link: _link})
          lastEvents.push({ type: _type, address: _address, text: _text, time: _time, link: _link });
        }
        if (_event.event === "UserStakeCollect") {
          const _type = 2;
          const _address = _event.returnValues.addr;
          const _text = `Stake Collected: ${_event.returnValues.rawAmount / 1e18} BNB`;
          const _time = moment.unix(_event.returnValues.timestamp).fromNow();
          const _link = `https://testnet.bscscan.com/tx/${_event.transactionHash}`;
          // console.log({address: _address, text: _text, time: _time, link: _link})
          lastEvents.push({ type: _type, address: _address, text: _text, time: _time, link: _link });
        }
        if (_event.event === "stake_sell_request") {
          const _type = 3;
          const _address = _event.returnValues.addr;
          const _text = `${_event.returnValues.rawAmount / 1e18} SEED Stake sell offer for ${_event.returnValues.price / 1e18} BNB`;
          const _time = moment.unix(_event.returnValues.timestamp).fromNow();
          const _link = `https://testnet.bscscan.com/tx/${_event.transactionHash}`;
          // console.log({address: _address, text: _text, time: _time, link: _link})
          lastEvents.push({ type: _type, address: _address, text: _text, time: _time, link: _link });
        }
      })
      if (lastEvents.length > 19) break;
      // console.log(blockNumber)
    }
    setRecentEvents(lastEvents);
    setTimeout(() => {
      getEvents();
    }, 30000);
  }

  return (
    <div className="event-bar">
      <a onClick={()=>setShowEvents(false)} className="close-icon">
        <img src={closeIcon}/>
      </a>
      <div className="title">
        Recent Events
      </div>
      <div className="event-list">
        {
          recentEvents?.map((_event, index) =>
            <a href={_event.link} target="_blank" className={`event-box-${_event.type}`} key={index}>
              <div className="address">{ellipsisAddr(_event.address)}</div>
              <div className="text">{_event.text}</div>
              <div className="time">{_event.time}</div>
            </a>
          )
        }
      </div>
    </div>
  )
}
