import React from "react";
import { Link } from "react-router-dom";
import logo from "../../assets/img/IOX_LOGO.png";

const HomeHeader = ()=>{
    return (
        <div className="header d-flex space-between">
            <div className="left">
                <img src={logo} />
            </div>
            <div className="center">
                 <a href="#about">About</a>
                 <a href="#about">Statistic</a>
                 <a href="#about">The Idea</a>
                 <a href="#about">Features</a>
                 <a href="#faq">FAQ</a>
                 <a href="/IOXPROJECT.pdf">White Paper</a>
            </div>
            <div className="right">
                <Link to="stake">Go Panel</Link>
            </div>
        </div>
    )
}

export default HomeHeader;