import { useState, useEffect } from 'react';
import { Routes, Route } from 'react-router-dom';
import './assets/styles/styles.scss';
import Layout from './layouts';

import Staking from './pages/Staking';
import Lobby from './pages/Lobby';
import BuyAndSell from './pages/BuyAndSell';
import TokensTransfer from './pages/TokensTransfer';

import { getContract } from './utils/contractFunctions';
import { getCookie } from './utils/functions';

import moment from "moment";
import Home from './pages/Home';

function App() {

  const [account, setAccount] = useState(null);
  const [page, setPage] = useState(0);
  const [contract, setContract] = useState(null);
  const [launchTime, setLaunchTime]  = useState(0);
  const [balance, setBalance] = useState(0);
  const [tokens, setTokens] = useState(0);
  const [currentDay, setCurrentDay] = useState(0);
  const [leftTime, setLeftTime] = useState({ hour: '00', min: '00', sec: '00' });
  const [referrer, setReferrer] = useState("0x0000000000000000000000000000000000000000")
  const [referralBonus, setReferralBonus] = useState(0);

  useEffect(async () => {
    if (!account) return;
    const _contract = await getContract(true);
    setContract(_contract);
  }, [account])

  useEffect(async () => {
    await getCurrentDay();
    await getLaunchTime();
  }, [contract])
  
  const getCurrentDay = async () => {
    if (!contract) return;
    const _currentDay = await contract.methods._clcDay().call();
    // console.log(_currentDay);
    setCurrentDay(_currentDay);
    setTimeout(() => {
      getCurrentDay();
    }, 10000)
  }

  const getLaunchTime = async () => {
    if (!contract) return;
    const _launchTime = await contract.methods.LAUNCH_TIME().call();
    setLaunchTime(parseInt(_launchTime) * 1000);
  }

  const getLeftTime = async () => {
    if (!account) return;
    // console.log(currentDay);
    if (!currentDay) return;
    if (!launchTime) return;
    let now = new Date().getTime() - new Date(launchTime).getTime();
    let t = 24 * 60 * 60 * 1000 - (now % (1000 * 60 * 60 * 24));
    let hours = Math.floor((t % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))
    let minutes = Math.floor((t % (1000 * 60 * 60)) / (1000 * 60))
    let seconds = Math.floor((t % (1000 * 60)) / 1000)

    if (hours.toString().length == 1) hours = "0" + hours
    if (minutes.toString().length == 1) minutes = "0" + minutes
    if (seconds.toString().length == 1) seconds = "0" + seconds
    setLeftTime({ hour: hours, min: minutes, sec: seconds });
  }

  const getReferralLink = () => {
    if (!account) {
      alert("Please connect your Metamask!")
    }
    const baseUrl = window.location.origin;
    const link = `${baseUrl}/?r=${account}`; // The URL of this website
    navigator.clipboard.writeText(link);
    alert("Link copied!");
  }

  const getReferrer = () => {
    let _referrer = new URLSearchParams(window.location.search).get("r");
    if (_referrer) {
      setReferrer(_referrer);
      document.cookie = `ref=${_referrer};path=/`;
    } else {
      _referrer = getCookie("ref");
      if (_referrer) setReferrer(_referrer);
    }
    if (_referrer == account) setReferrer("0x0000000000000000000000000000000000000000")
  }

  const getReferralBonus = async () => {
    if(!account) return;
    if(!contract) return;
    const _referralBonus = await contract.methods.referrerBonusesPaid(account).call();
    setReferralBonus(_referralBonus);
  }

  useEffect(() => {
    getReferrer();
    getReferralBonus();
  }, [account])

  useEffect(() => {
    setInterval(() => {
      getLeftTime();
    }, 1000)
  }, [contract, currentDay, launchTime])

  // Launch this contract (only owner)

  // useEffect(async () => {
  //   if(!contract) return;
  //   const receipt = await contract.methods.launch().send({from: account});
  //   console.log(receipt);
  // }, [contract])

  return (
    <>
      <Routes>
        <Route index element={<Home></Home>}></Route>
        <Route exact path="stake" element={
        <Layout account={account} contract={contract} page={page} currentDay={currentDay} leftTime={leftTime}>
          <Staking account={account} setAccount={setAccount} setPage={setPage} contract={contract} balance={balance} setBalance={setBalance} tokens={tokens} setTokens={setTokens} currentDay={currentDay} getReferralLink={getReferralLink} referralBonus={referralBonus} />
        </Layout>
        } />
        <Route exact path="lobby" element={
          <Layout account={account} contract={contract} page={page} currentDay={currentDay} leftTime={leftTime}>
            <Lobby account={account} setAccount={setAccount} setPage={setPage} contract={contract} balance={balance} setBalance={setBalance} tokens={tokens} setTokens={setTokens} currentDay={currentDay} getReferralLink={getReferralLink} referrer={referrer} referralBonus={referralBonus} />
          </Layout>
        }/>
        <Route exact path="buy_sell" element={
          <Layout account={account} contract={contract} page={page} currentDay={currentDay} leftTime={leftTime}>
            <BuyAndSell account={account} setAccount={setAccount} setPage={setPage} contract={contract} balance={balance} setBalance={setBalance} tokens={tokens} setTokens={setTokens} currentDay={currentDay} getReferralLink={getReferralLink} referralBonus={referralBonus} />
          </Layout>
        } />
        <Route exact path="transfer" element={
          <Layout account={account} contract={contract} page={page} currentDay={currentDay} leftTime={leftTime}>
            <TokensTransfer account={account} setAccount={setAccount} setPage={setPage} contract={contract} balance={balance} setBalance={setBalance} tokens={tokens} setTokens={setTokens} currentDay={currentDay} getReferralLink={getReferralLink} referralBonus={referralBonus} />
          </Layout>
        } />
      </Routes>
    </>
  );
}

export default App;
