import React, { useEffect, useState } from 'react';
import Web3 from 'web3';
import { MAIN_COIN, TOKEN_NAME } from '../contracts/contractInfo';

import { getContract } from '../utils/contractFunctions';
import WalletConnect from '../utils/WalletConnect';

export default function TokensTransfer({ account, setAccount, setPage, contract, balance, setBalance, tokens, setTokens, currentDay, getReferralLink, referralBonus }) {

  const [transferAmount, setTransferAmount] = useState(null);
  const [receiveAccount, setReceiveAccount] = useState(null);
  const [transferButton, setTransferButton] = useState("Transfer");

  const [checkFlag, setCheckFlag] = useState([]);
  const [buttonFlag, setButtonFlag] = useState(false);

  const doTransfer = async () => {
    if (!account) {
      alert("Wallet is not connected!");
      return;
    }
    setTransferButton("Transfer...");
    try {
      console.log(contract);
      const receipt = await contract.methods.transfer(receiveAccount, Web3.utils.toWei(transferAmount, "ether")).send({ from: account });
      alert(`Successfully transfered ${transferAmount} ${TOKEN_NAME} to ${receiveAccount}.`);
      console.log(receipt);
    } catch (err) {
      console.log(err);
      alert("Something went wrong!");
    }
    setTransferButton("Transfer");
  }

  useEffect(() => {
    if (!tokens) return;
    let temp = [];
    temp[0] = transferAmount <= 0 ? 1 : tokens < Number(transferAmount) ? 2 : 0;
    temp[1] = !Web3.utils.isAddress(receiveAccount) ? 1 : 0;
    setCheckFlag(temp);
    setButtonFlag(!temp[0] * !temp[1]);
  }, [account, transferAmount, receiveAccount])

  useEffect(() => {
    setPage(3);
  })

  return (
    <div className="transfer">
      <div className="center">
        <div className="left">
          <div className="stake">
            <div className="title">Transfer</div>
            <div className="intro">Send your tokens to the other user.</div>
            <div className="function">
              <div>
                <input type="number" value={transferAmount} placeholder="Tokens to send" onChange={(e) => setTransferAmount(e.target.value)}></input>
                {
                  checkFlag[0] === 1
                    ? <span>Invaild input data</span>
                    : checkFlag[0] === 2
                      ? <span>No enough balance</span>
                      : null
                }
              </div>
              <div>
                <input type="text" value={receiveAccount} placeholder="Address of receiver" onChange={(e) => setReceiveAccount(e.target.value)}></input>
                {
                  checkFlag[1] === 1
                    ? <span>Invaild input data</span>
                    : null
                }
              </div>
              <button onClick={doTransfer} disabled={!buttonFlag}>{transferButton}</button>
            </div>
          </div>
        </div>
        <div className="right">
          <WalletConnect account={account} setAccount={setAccount} setBalance={setBalance} setTokens={setTokens} />
          <div className="info">
            <div className="title">Balance (${MAIN_COIN})</div>
            <div className="content">{Number(balance).toFixed(4)}</div>
          </div>
          <div className="info">
            <div className="title">Tokens (${TOKEN_NAME})</div>
            <div className="content">{Number(tokens).toFixed(1)}</div>
          </div>
          <div className="info">
            <div className="title">Referral Bonus (${TOKEN_NAME})</div>
            <div className="content">{(Number(referralBonus)/1e18).toFixed(1)}</div>
          </div>
          <button className="referrer-link" onClick={getReferralLink}>REFERRAL LINK</button>
        </div>
      </div>
    </div>
  )
}
