import React, { useState } from "react";
import { MAIN_COIN, TOKEN_NAME } from "../../contracts/contractInfo";
import './faq.scss';
const FaqPage = ()=>{
    const [faqs, setFaqs] = useState([
        {
            id: 1,
            q: "How is the stake pool calculated?",
            answer: `94% of the ${MAIN_COIN} that enters the Daily Auction lobby is pooled and sent back to stakers`,
            isOpen: false
        },
        {
            id: 2,
            q: `Do I have to stake $${TOKEN_NAME} to receive ${MAIN_COIN} dividends?`,
            answer: `Yes! The whole platform is based on staking and in order to receive ${MAIN_COIN} you need to stake your ${TOKEN_NAME}.`,
            isOpen: false
        },
        {
            id: 3,
            q: "When will I receive my dividends?",
            answer: "You can collect your stakes when they are finished and receive your dividends.",
            isOpen: false
        },
        {
            id: 4,
            q: "What is the point of the auction?",
            answer: `Since there is no liquidity on a DEX, the main way users can acquire ${TOKEN_NAME} is buy participating in the daily auctions.`,
            isOpen: false
        },
        {
            id: 5,
            q: `Where does the $${MAIN_COIN} go?`,
            answer: `94% of all $${MAIN_COIN} collected is returned to the stakers. The other 6% is reserved for development costs and buy backs!`,
            isOpen: false
        }
    ]);

    const faqHeaderClick = (id)=>{
        let _faqs = faqs;
        _faqs.map((item, key)=>{
            if(item.id != id){
                item.isOpen = false;
            }
        });

        _faqs[id - 1].isOpen = !_faqs[id - 1].isOpen;
        console.log(_faqs);
        setFaqs([..._faqs]);
    }

    return <div className="faq">
        <div className="container">
            <h3 className="title">Frequency Asked Questions</h3>
            <div className="content">
                {
                    faqs.map((item, key)=>
                        <div className="faq-item" key={key}>
                            <a className="question" onClick={()=>faqHeaderClick(item.id)}>
                                {item.q}
                                {
                                    item.isOpen?
                                    <span>-</span>
                                    :
                                    <span>+</span>
                                }
                            </a>
                            {
                                item.isOpen?
                                <div className="answer">
                                    <p>{item.answer}</p>
                                </div>
                                :
                                <></>
                            }
                        </div>
                    )
                }
            </div>
        </div>
    </div>
}

export default FaqPage;