export function getCookie(cname) {
  let name = cname + "=";
  let decodedCookie = decodeURIComponent(document.cookie);
  let ca = decodedCookie.split(';');
  for (let i = 0; i < ca.length; i++) {
    let c = ca[i];
    while (c.charAt(0) === ' ') {
      c = c.substring(1);
    }
    if (c.indexOf(name) === 0) {
      return c.substring(name.length, c.length);
    }
  }
  return "";
}

export const estimateBonusTokens = (_stakeAmount, _stakeDays) => {
  return (_stakeAmount * (_stakeDays ** 2) * 128) / 1e7;
}

export function ellipsisAddr(_addr) {
  return String(_addr).substring(0, 6) + "..." + String(_addr).substring(38)
}