import React, { useEffect, useState } from 'react';
import Web3 from 'web3';

import { estimateBonusTokens } from '../utils/functions';

import WalletConnect from '../utils/WalletConnect';

import close from "../assets/img/close_btn.svg"
import { MAIN_COIN, PROJECT_NAME, TOKEN_NAME } from '../contracts/contractInfo';
import { Link } from 'react-router-dom';

export default function BuyAndSell({ account, setAccount, setPage, contract, balance, setBalance, tokens, setTokens, currentDay, getReferralLink, referralBonus }) {

  const [overallTradeAmount, setOverallTradeAmount] = useState(0);
  const [overallStakesSold, setOverallStakesSold] = useState(0);
  const [soldStakeFunds, setSoldStakeFunds] = useState(0);
  const [tradeAmount, setTradeAmount] = useState(0);

  const [activeStakeData, setActiveStakeData] = useState([]);
  const [sellingStakeData, setSellingStakeData] = useState([]);

  const [selectedStakeId, setSelectedStakeId] = useState(null);
  const [isWindowOpened, setIsWindowOpened] = useState(false);
  const [offerPrice, setOfferPrice] = useState(null);
  const [offerButton, setOfferButton] = useState("Create a sell offer")

  const [checkFlag, setCheckFlag] = useState([]);
  const [buttonFlag, setButtonFlag] = useState(false);

  const getValues = async () => {
    if (!contract) return;
    const _overallTradeAmount = await contract.methods.totalTradeAmount().call();
    const _overallStakesSold = await contract.methods.totalStakesSold().call();
    const _tradeAmount = await contract.methods.totalStakeTradeAmount(account).call();
    setOverallTradeAmount(Web3.utils.fromWei(_overallTradeAmount, "ether"));
    setOverallStakesSold(_overallStakesSold);
    setTradeAmount(_tradeAmount);
    setTimeout(() => {
      getValues();
    }, 5000)
  }

  const getActiveStakeData = async () => {
    if (!contract) return;
    if (!currentDay) return;
    let temp = [];
    let i = 0;
    while (true) {
      const _data = await contract.methods.mapMemberStake(account, i).call();
      // console.log(_data);
      const _stakeId = _data.stakeId;
      const _startDay = _data.startDay;
      const _endDay = _data.endDay;
      const _progress = Number(currentDay) >= Number(_endDay) ? 100 : Number(currentDay) <= Number(_startDay) ? 0 : (Number(currentDay) + 1 - Number(_startDay)) * 100 / (Number(_endDay) - Number(_startDay) + 1);
      const _staked = _data.tokenValue;
      const _BNBDivs = await contract.methods.calcStakeCollecting(account, i).call();
      const _bonusTokens = estimateBonusTokens(Web3.utils.fromWei(_staked, "ether"), _endDay - _startDay);
      const _hasCollected = _data.stakeCollected;
      const _hasSold = _data.stake_hasSold;
      const _forSell = _data.stake_forSell;
      const _hasLoan = _data.stake_hasLoan;
      const _forLoan = _data.stake_forLoan;
      if (_startDay == 0) break;
      if (_progress === 100 || _hasSold) {
        i++;
        continue;
      }
      temp[i] = {
        stakeId: _stakeId,
        startDay: _startDay,
        endDay: _endDay,
        progress: _progress,
        staked: _staked,
        BNBDivs: _BNBDivs,
        bonusTokens: _bonusTokens,
        hasCollected: _hasCollected,
        hasSold: _hasSold,
        forSell: _forSell,
        hasLoan: _hasLoan,
        forLoan: _forLoan,
      }
      // console.log(temp[id - 1]);
      i++;
    }
    // console.log(temp);
    setActiveStakeData(temp);
  }

  const getSellingStakeData = async () => {
    if (!contract) return;
    // if (!currentDay) return;
    let temp = [];
    let blockNumber = await window.web3.eth.getBlockNumber();
    for (; blockNumber > 21165104; blockNumber = blockNumber - 5001) {
      const fromBlock = (blockNumber - 5000) < 0 ? 0 : (blockNumber - 5000);
      const _events = await contract.getPastEvents('AllEvents', {
        fromBlock: fromBlock,
        toBlock: blockNumber,
      });
      _events.reverse();
      // console.log(_events);
      _events.map(async (_event) => {
        if (_event.event === "stake_sell_request") {
          const _seller = _event.returnValues.addr;
          const _staked = _event.returnValues.rawAmount / 1e18;
          const _price = _event.returnValues.price / 1e18;
          const _stakeId = _event.returnValues.stakeId;
          const _BNBDivs = await contract.methods.calcStakeCollecting(_seller, _stakeId).call();
          const { startDay, endDay, stake_hasSold } = await contract.methods.mapMemberStake(_seller, _stakeId).call();
          const _bonusTokens = estimateBonusTokens(String(_staked), endDay - startDay);
          // console.log(_event);
          if (!stake_hasSold) {
            temp.push({ seller: _seller, stakeId: _stakeId, startDay: startDay, endDay: endDay, staked: _staked, BNBDivs: _BNBDivs, bonusTokens: _bonusTokens, price: _price });
          }
        }
      })
      // console.log(blockNumber)
    }
    // console.log(temp);
    setSellingStakeData(temp);
  }

  const sellStakeFinal = async () => {
    if (!account) {
      alert("Wallet is not connected!");
      return;
    }
    if (!contract) return;
    setOfferButton("... ...");
    try {
      const _stakeSellingIsPaused = await contract.methods.stakeSellingIsPaused().call();
      if (_stakeSellingIsPaused) {
        alert("Selling is paused!");
      } else {
        const receipt = await contract.methods.sellStakeRequest(selectedStakeId, Web3.utils.toWei(offerPrice, "ether")).send({ from: account, shouldPollResponse: true })
        console.log(receipt);
        alert(`Successfully created offer of ${offerPrice} ${MAIN_COIN} to sell stake`);
        getActiveStakeData();
        getSellingStakeData();
      }
    } catch (err) {
      console.log(err);
      alert("Something went wrong!");
    }
    setOfferButton("Create a sell offer");
    setIsWindowOpened(false);
  }

  const buyStakeFinal = async (_seller, _stakeId, _price) => {
    if (!account) {
      alert("Wallet is not connected!");
      return;
    }
    if (!contract) return;
    console.log(_seller, account);
    if (_seller.toLowerCase() === account) {
      alert("It is your stake!");
      return;
    }
    try {
      const _stakeSellingIsPaused = await contract.methods.stakeSellingIsPaused().call();
      if (_stakeSellingIsPaused) {
        alert("Selling is paused!");
      } else {
        const receipt = await contract.methods.buyStakeRequest(_seller, _stakeId).send({ from: account, value: Web3.utils.toWei(String(_price), "ether"), shouldPollResponse: true })
        console.log(receipt);
        alert(`Successfully bought a stake for ${_price} ${MAIN_COIN}`);
        getActiveStakeData();
        getSellingStakeData();
      }
    } catch (err) {
      console.log(err);
      alert("Something went wrong!");
    }

  }

  const getSoldStakeFunds = async () =>{
    const res = await contract.methods.soldStakeFunds(account).call({shouldPollResponse: true});
    console.log('getSoldStakeFunds ---------------', res);
    setSoldStakeFunds(parseInt(res) / 1e18);
  }

  useEffect(() => {
    getActiveStakeData();
    getSoldStakeFunds();
  }, [contract, currentDay, account]);

  useEffect(async () => {
    getSellingStakeData();
  }, [account, contract])

  useEffect(() => {
    getValues();
  }, [contract]);

  useEffect(() => {
    if (!balance) return;
    let temp = [];
    temp[0] = offerPrice <= 0 ? 1 : 0;
    setCheckFlag(temp);
    setButtonFlag(!temp[0]);
  }, [account, offerPrice])

  useEffect(() => {
    setPage(2);
  })

  return (
    <div className="buy-and-sell">
      <div className="top">
        <div className="info-card-1">
          <div className="title">Overall Trade Amount</div>
          <div className="content">{Number(overallTradeAmount).toFixed(2)}</div>
        </div>
        <div className="info-card-2">
          <div className="title">Overall Stakes Sold</div>
          <div className="content">{overallStakesSold}</div>
        </div>
        <div className="info-card-3">
          <div className="title">You have - {Number(soldStakeFunds).toFixed(4)} ${MAIN_COIN} - collectable from your sold stakes!</div>
        </div>
        <Link to="/">
          <div className="title">What is {PROJECT_NAME}?</div>
          <div className="content">Learn more...</div>
        </Link>
      </div>
      <div className="center">
        <div className="left">
          <div className="list">
            <div className="title">Your active stakes</div>
            <div style={{width: '100%', overflow: 'auto', maxHeight: '500px'}}>
            <table>
              <thead>
                <tr>
                  <th>Start</th>
                  <th>End</th>
                  <th>Progress</th>
                  <th>Staked</th>
                  <th>${MAIN_COIN} divs</th>
                  <th>Bonus Tokens (final)</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                {activeStakeData.map((data, index) =>
                  <tr key={index}>
                    <td>{data.startDay}</td>
                    <td>{data.endDay}</td>
                    <td>
                      {
                        data.progress === 0
                          ? "start soon"
                          : data.progress === 100
                            ? "completed"
                            : data.progress.toFixed(0) + "%"
                      }
                    </td>
                    <td>{(data.staked / 1e18).toFixed(2)}</td>
                    <td>{(data.BNBDivs / 1e18).toFixed(4)}</td>
                    <td>{(data.bonusTokens).toFixed(4)}</td>
                    <td>
                      {
                        data.forSell
                          ? <button className="selling-btn" onClick={() => { }}>Selling</button>
                          : <button className="sell-btn" onClick={() => { setIsWindowOpened(true); setSelectedStakeId(data.stakeId) }}>Sell</button>
                      }

                    </td>
                  </tr>
                )}
              </tbody>
            </table>
            {
              activeStakeData.length === 0 && <div className="warning">You have no stakes!</div>
            }
            </div>
            
          </div>
          <div className="list">
            <div className="title">For sell stakes({sellingStakeData.length})</div>
            <div className="content" style={{flexWrap: 'wrap'}}>
              {
                sellingStakeData.map((data, index) =>
                  <div className="sell-card" key={index}>
                    <div className="days">
                      <span>Day {data.startDay}</span>
                      <span>Day {data.endDay}</span>
                    </div>
                    <div className="progress">
                      <div style={{ background: "linear-gradient(to left, #f3dc57 0%, #ffa92a 100%)", width: `${(currentDay + 1 - data.startDay) / (data.endDay - data.startDay)}%`, height: "100%", borderRadius: "50vmax" }}></div>
                    </div>
                    <div className="info-item">
                      <span className="info-title">Staked:</span>
                      <span className="info-content">{data.staked} {TOKEN_NAME}</span>
                    </div>
                    <div className="info-item">
                      <span className="info-title">Dividends:</span>
                      <span className="info-content">{data.BNBDivs} ${MAIN_COIN}</span>
                    </div>
                    <div className="info-item">
                      <span className="info-title">Bonus:</span>
                      <span className="info-content">{data.bonusTokens} {TOKEN_NAME}</span>
                    </div>
                    <div className="buy-item" onClick={() => buyStakeFinal(data.seller, data.stakeId, data.price)}>Buy for {data.price} ${MAIN_COIN}</div>
                  </div>
                )

              }

            </div>
            {
              sellingStakeData.length === 0 && <div className="warning">You have no stakes!</div>
            }
          </div>
        </div>
        <div className="right">
          <WalletConnect account={account} setAccount={setAccount} setBalance={setBalance} setTokens={setTokens} />
          <div className="info">
            <div className="title">Balance (${MAIN_COIN})</div>
            <div className="content">{Number(balance).toFixed(4)}</div>
          </div>
          <div className="info">
            <div className="title">Tokens (${TOKEN_NAME})</div>
            <div className="content">{Number(tokens).toFixed(1)}</div>
          </div>
          <div className="info">
            <div className="title">Trade Amount (${MAIN_COIN})</div>
            <div className="content">{(Number(tradeAmount)/1e18).toFixed(4)}</div>
          </div>
          <div className="info">
            <div className="title">Referral Bonus (${TOKEN_NAME})</div>
            <div className="content">{(Number(referralBonus)/1e18).toFixed(1)}</div>
          </div>
          <button className="referrer-link" onClick={getReferralLink}>REFERRAL LINK</button>
        </div>
      </div>
      {
        isWindowOpened
          ? <div className="backdrop">
            <div className="auction-input">
              <div className="top">
                <img onClick={() => setIsWindowOpened(false)} src={close} />
              </div>
              <div className="center">
                Sell price (${MAIN_COIN}) for the stake
                <input value={offerPrice} onChange={(e) => setOfferPrice(e.target.value)} placeholder="0.1"></input>
                {
                  checkFlag[0] === 1
                    ? <span>Invaild input data</span>
                    : null
                }
              </div>
              <div className="bottom">
                <button onClick={sellStakeFinal} disabled={!buttonFlag}>{offerButton}</button>
              </div>
            </div>
          </div>
          : null
      }
    </div>
  )
}
