import React from "react";
import logo from "../../assets/img/IOX_LOGO.png";
import twitterIcon from "../../assets/img/twitter.png";
import telegramIcon from "../../assets/img/telegram.png";
import discordIcon from "../../assets/img/discord.png";
import youtubeIcon from "../../assets/img/youtube.png";
import { CONTACT_MAIL, PROJECT_NAME } from "../../contracts/contractInfo";
import './footer.scss';
const HomeFooter = ()=>{
    return (
        <footer className="footer">
            <div className="container">
                <div className="social-items">
                    <div className="col-lg-4">
                        <a href="" className="logo">
                            <img className="logo__img" src={logo} alt=""/>
                        </a>
                        <div className="copyright">@ {new Date().getFullYear()}, {PROJECT_NAME}</div>
                    </div>
                    <div className="col-lg-4">
                        <div className="social-block">
                            <div className="social-block__title">
                                Stay connected:
                            </div>
                            <ul className="social-list">
                                <li className="social-list__item">
                                    <a href="https://twitter.com/iox_platform" className="social-list__link">
                                        <img src={twitterIcon} className="icon" />              
                                    </a>
                                </li>
                                <li className="social-list__item">
                                    <a href="https://t.me/IOXPlatform" className="social-list__link">
                                        <img src={telegramIcon} className="icon" />
                                    </a>
                                </li>
                                <li className="social-list__item">
                                    <a href="https://discord.gg/JYFGqRkr" className="social-list__link" style={{textAlign: "center",
                                        paddingTop: '6px'}}>
                                        <img src={discordIcon} className="icon" />
                                    </a>
                                </li>
                                <li className="social-list__item">
                                    <a href="https://youtube.com/channel/UCpiyaENEU05Orl3vnXWQR6g" className="social-list__link" style={{textAlign: "center",
                                        paddingTop: '6px'}}>
                                        <img src={youtubeIcon} className="icon" />
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div className="col-lg-4">
                        <div className="social-block">
                            <div className="social-block__title">
                                Contact us
                            </div>
                            <span>{CONTACT_MAIL}</span>
                        </div>
                    </div>
                </div>
            </div>
        </footer>
    )
}

export default HomeFooter;