import React, { useEffect, useState } from 'react';
import Web3 from 'web3';
import { Link } from "react-router-dom";

import WalletConnect from '../utils/WalletConnect';

import { CONTRACT_ADDRESS, MAIN_COIN, PROJECT_NAME, TOKEN_NAME } from "../contracts/contractInfo";
import { estimateBonusTokens } from '../utils/functions';

export default function Staking({ account, setAccount, setPage, contract, balance, setBalance, tokens, setTokens, currentDay, getReferralLink, referralBonus }) {

  const [overallStakedTokens, setOverallStakeTokens] = useState(0);
  const [overallCollectedDivs, setOverallCollectedDivs] = useState(0);
  const [overallCollectedBonus, setOverallCollectedBonus] = useState(0);

  const [estimatedBonus, setEstimatedBonus] = useState(0);
  const [todayStakedTokens, setTodayStakedTokens] = useState(0);
  const [totalAvailableDivs, setTotalAvailableDivs] = useState(0);

  const [stakeAmount, setStakeAmount] = useState(null);
  const [stakeDays, setStakeDays] = useState(null);
  const [stakeButton, setStakeButton] = useState("Stake");

  const [stakeData, setStakeData] = useState([]);

  const [userStakeData, setUserStakeData] = useState({ staked: 0, divs: 0 });

  const [checkFlag, setCheckFlag] = useState([]);
  const [buttonFlag, setButtonFlag] = useState(false);

  const doStake = async () => {
    if (!account) {
      alert("Wallet is not connected!");
      return;
    }
    if (!contract) return;
    setStakeButton("Staking...");
    try {
      // console.log(contract);
      const receipt = await contract.methods.EnterStake(Web3.utils.toWei(stakeAmount, "ether"), parseInt(stakeDays)).send({ from: account, shouldPollResponse: false });
      console.log(receipt);
      alert(`Successfully staked ${stakeAmount} ${TOKEN_NAME} for ${parseInt(stakeDays)} days.`);
      getStakeData();
    } catch (err) {
      console.log(err);
      alert("Something went wrong!");
    }
    setStakeButton("Stake");
  }

  const endStake = async (_stakeId) => {
    if (!account) {
      alert("Wallet is not connected!");
      return;
    }
    if (!contract) return;
    try {
      const receipt = await contract.methods.EndStake(_stakeId).send({ from: account, shouldPollResponse: false });
      console.log(receipt)
      alert("Successfully collected!");
      getStakeData();
    } catch (err) {
      console.log(err);
      alert("Something went wrong!")
    }
  }

  const getStakeData = async () => {
    if (!contract) return;
    if (!currentDay) return;
    let temp = [];
    let i = 0;
    while (true) {
      const _data = await contract.methods.mapMemberStake(account, i).call();
      // console.log(_data);
      const _stakeId = _data.stakeId;
      const _startDay = _data.startDay;
      const _endDay = _data.endDay;
      const _progress = Number(currentDay) >= Number(_endDay) ? 100 : Number(currentDay) <= Number(_startDay) ? 0 : (Number(currentDay) + 1 - Number(_startDay)) * 100 / (Number(_endDay) - Number(_startDay) + 1);
      const _staked = _data.tokenValue;
      const _BNBDivs = await contract.methods.calcStakeCollecting(account, _stakeId).call();
      const _bonusTokens = estimateBonusTokens(Web3.utils.fromWei(_staked, "ether"), _endDay - _startDay);
      const _hasCollected = _data.stakeCollected;
      const _hasSold = _data.stake_hasSold;
      const _forSell = _data.stake_forSell;
      const _hasLoan = _data.stake_hasLoan;
      const _forLoan = _data.stake_forLoan;
      if (_startDay == 0) break;
      temp[i] = {
        stakeId: _stakeId,
        startDay: _startDay,
        endDay: _endDay,
        progress: _progress,
        staked: _staked,
        BNBDivs: _BNBDivs,
        bonusTokens: _bonusTokens,
        hasCollected: _hasCollected,
        hasSold: _hasSold,
        forSell: _forSell,
        hasLoan: _hasLoan,
        forLoan: _forLoan,
      }
      // console.log(temp[i]);
      i++;
    }
    // console.log(temp);
    setStakeData(temp);
  }

  const getValues = async () => {
    if (!contract) return;
    const _overallStakedTokens = await contract.methods.overall_stakedTokens().call();
    const _overallCollectedDivs = await contract.methods.overall_collectedDivs().call();
    const _overallCollectedBonus = await contract.methods.overall_collectedTokens().call();
    const _todayStakedTokens = await contract.methods.daysActiveInStakeTokensIncrese(currentDay + 1).call();
    const _totalAvailableDivs = await window.web3.eth.getBalance(CONTRACT_ADDRESS);
    const _userStakeData = await contract.methods.mapMemberLobby_overallData(account).call();
    setOverallStakeTokens(Web3.utils.fromWei(_overallStakedTokens, "ether"));
    setOverallCollectedDivs(Web3.utils.fromWei(_overallCollectedDivs, "ether"));
    setOverallCollectedBonus(Web3.utils.fromWei(_overallCollectedBonus, "ether"));
    setTodayStakedTokens(Web3.utils.fromWei(_todayStakedTokens), "ether");
    setTotalAvailableDivs(Web3.utils.fromWei(_totalAvailableDivs) * 94 / 100);
    setUserStakeData({ staked: Web3.utils.fromWei(_userStakeData.overall_stakedTokens, "ether"), divs: Web3.utils.fromWei(_userStakeData.overall_collectedDivs, "ether") });
    setTimeout(() => {
      getValues()
    }, 5000);
  }

  useEffect(() => {
    if (!tokens) return;
    let temp = [];
    temp[0] = stakeAmount <= 0 ? 1 : tokens < Number(stakeAmount) ? 2 : 0;
    temp[1] = stakeDays <= 0 || stakeDays > 300 ? 1 : 0;
    if (temp[0] !== 1 || !temp[1]) setEstimatedBonus(estimateBonusTokens(stakeAmount, stakeDays));
    setCheckFlag(temp);
    setButtonFlag(!temp[0] * !temp[1]);
  }, [account, stakeAmount, stakeDays])

  useEffect(() => {
    getStakeData();
  }, [contract, currentDay, account]);

  useEffect(() => {
    getValues();
  }, [contract]);

  useEffect(() => {
    setPage(0);
  })

  return (
    <div className="staking">
      <div className="top">
        <div className="info-card-1">
          <div className="title">Overall Staked Tokens</div>
          <div className="content">{Number(overallStakedTokens).toFixed(2)}</div>
        </div>
        <div className="info-card-2">
          <div className="title">Overall Collected Dividends</div>
          <div className="content">{Number(overallCollectedDivs).toFixed(2)}</div>
        </div>
        <div className="info-card-3">
          <div className="title">Overall Collected Bonus Tokens</div>
          <div className="content">{Number(overallCollectedBonus).toFixed(2)}</div>
        </div>
        <Link to="/">
          <div className="title">What is ${PROJECT_NAME}?</div>
          <div className="content">Learn more...</div>
        </Link>
      </div>
      <div className="center">
        <div className="left">
          <div className="stake">
            <div className="title">Stake</div>
            <div className="intro">Stake your Tokens to earn Dividends and Bonus Tokens on them.</div>
            <div className="function">
              <input type="number" value={stakeAmount} placeholder="Tokens to stake" onChange={(e) => setStakeAmount(e.target.value)}></input>
              {
                checkFlag[0] === 1
                  ? <span>Invaild input data</span>
                  : checkFlag[0] === 2
                    ? <span>No enough tokens</span>
                    : null
              }
              <input type="number" value={stakeDays} placeholder="Stake days" onChange={(e) => setStakeDays(e.target.value)}></input>
              {
                checkFlag[1] === 1
                  ? <span>Invaild input data</span>
                  : null
              }
              <button onClick={doStake} disabled={!buttonFlag}>{stakeButton}</button>
            </div>
            <div className="bottom">
              <div className="info">
                <div className="title">Bonus you will receive:</div>
                <div className="content">{Number(estimatedBonus).toFixed(2)} ${TOKEN_NAME}</div>
              </div>
              <div className="info">
                <div className="title">Today Staked Tokens:</div>
                <div className="content">{Number(todayStakedTokens).toFixed(2)} ${TOKEN_NAME}</div>
              </div>
              <div className="info">
                <div className="title">Total available dividends:</div>
                <div className="content">{Number(totalAvailableDivs).toFixed(4)} ${MAIN_COIN}</div>
              </div>
            </div>
          </div>
          <div className="list">
            <div className="title">Your stakes</div>
            <div style={{width: '100%', overflow: 'auto', maxHeight: '500px'}}>
            <table>
              <thead>
                <tr>
                  <th>Start</th>
                  <th>End</th>
                  <th>Progress</th>
                  <th>Staked</th>
                  <th>${MAIN_COIN} divs</th>
                  <th>Bonus Tokens (final)</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                {stakeData.map((data, index) =>
                  <tr key={index}>
                    <td>{data.startDay}</td>
                    <td>{data.endDay}</td>
                    <td>
                      {
                        data.progress === 0
                          ? "start soon"
                          : data.progress === 100
                            ? "completed"
                            : data.progress.toFixed(0) + "%"
                      }

                    </td>
                    <td>{(data.staked / 1e18).toFixed(2)}</td>
                    <td>{(data.BNBDivs / 1e18).toFixed(4)}</td>
                    <td>{(data.bonusTokens).toFixed(4)}</td>
                    <td>
                      {
                        data.progress === 100
                          ? data.hasCollected
                            ? <button className="collected-btn" >Collected</button>
                            : <button className="collect-btn" onClick={() => endStake(data.stakeId)} >Collect</button>
                          : data.hasSold
                            ? <button className="sold-btn" >Sold</button>
                            : data.forSell
                              ? <button className="selling-btn"><Link to="/buy_sell">Selling</Link></button>
                              : <button className="sell-btn"><Link to="/buy_sell">Sell</Link></button>
                      }
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
            {
              stakeData.length === 0 && <div className="warning">You have no stakes!</div>
            }
            </div>
          </div>
        </div>
        <div className="right">
          <WalletConnect account={account} setAccount={setAccount} setBalance={setBalance} setTokens={setTokens} />
          <div className="info">
            <div className="title">Balance (${MAIN_COIN})</div>
            <div className="content">{Number(balance).toFixed(4)}</div>
          </div>
          <div className="info">
            <div className="title">Tokens (${TOKEN_NAME})</div>
            <div className="content">{Number(tokens).toFixed(1)}</div>
          </div>
          <div className="info">
            <div className="title">Your all-time staked (${TOKEN_NAME})</div>
            <div className="content">{Number(userStakeData.staked).toFixed(1)}</div>
          </div>
          <div className="info">
            <div className="title">Your all-time divs (${MAIN_COIN})</div>
            <div className="content">{Number(userStakeData.divs).toFixed(4)}</div>
          </div>
          <div className="info">
            <div className="title">Referral Bonus (${TOKEN_NAME})</div>
            <div className="content">{(Number(referralBonus)/1e18).toFixed(1)}</div>
          </div>
          <button className="referrer-link" onClick={getReferralLink}>REFERRAL LINK</button>
        </div>
      </div>

    </div>

  )
}
