import React, { useRef, useState } from 'react';
import Header from "./Header";
import EventBar from "./EventBar";

export default function Layout({ children, account, contract, page, currentDay, leftTime }) {
  const [showEvents, setShowEvents] = useState(false);
  const eventBarElement = useRef();
  return (
    <>
      <Header page={page} currentDay={currentDay} leftTime={leftTime} showEvents={showEvents} setShowEvents={setShowEvents} />
      <div className="page-content">
        <EventBar account={account} contract={contract} />
        {
          showEvents && 
          <div className='mobile-show'>
            <EventBar ref={eventBarElement} account={account} contract={contract} showEvents={showEvents} setShowEvents={setShowEvents} />
          </div>
        }
        {children}
      </div>
    </>
  )
}
