import Web3 from 'web3';
import { CONTRACT_ADDRESS, CONTRACT_ABI, RPC } from '../contracts/contractInfo';

const web3 = new Web3();

export const getContract = async (signer) => {
    if (signer)
      window.web3 = new Web3(window.ethereum);
    else
      window.web3 = new Web3(RPC);
    const contract = await new window.web3.eth.Contract(CONTRACT_ABI, CONTRACT_ADDRESS);
    return contract;
  };

export const getTokens = async (_account) => {
  if(!_account) return 0;
  const contract = await getContract();
  const _tokens = await contract.methods.balanceOf(_account).call();
  return web3.utils.fromWei(_tokens, "ether");
}

export const getBalance = async (_account) => {
  if(!_account) return 0;
  const _balance= await window.web3.eth.getBalance(_account);
  return web3.utils.fromWei(_balance, "ether");
}

export const timeSince = (date)=>{
	var seconds = Math.floor((new Date() - date) / 1000);
	var interval = seconds / 31536000;
  
	if (interval > 1) {
	  return Math.floor(interval) + " year/s";
	}
	interval = seconds / 2592000;
	if (interval > 1) {
	  return Math.floor(interval) + " month/s";
	}
	interval = seconds / 86400;
	if (interval > 1) {
	  return Math.floor(interval) + " day/s";
	}
	interval = seconds / 3600;
	if (interval > 1) {
	  return Math.floor(interval) + " hour/s";
	}
	interval = seconds / 60;
	if (interval > 1) {
	  return Math.floor(interval) + " minute/s";
	}
	return Math.floor(seconds) + " second/s";
}