import React, { useEffect, useState } from 'react';
import Web3 from 'web3';

import WalletConnect from '../utils/WalletConnect';

import close from "../assets/img/close_btn.svg";
import { MAIN_COIN, PROJECT_NAME, TOKEN_NAME } from '../contracts/contractInfo';
import { Link } from 'react-router-dom';

export default function Lobby({ account, setAccount, setPage, contract, balance, setBalance, tokens, setTokens, currentDay, getReferralLink, referrer, referralBonus }) {

  const [overallParticipants, setOverallParticipants] = useState(0);
  const [overallBNBDeposits, setOverallBNBDeposits] = useState(0);
  const [overallDistributedTokens, setOverallDistributedTokens] = useState(0);
  const [lobbyData, setLobbyData] = useState([]);

  const [isWindowOpened, setIsWindowOpened] = useState(false);
  const [auctionAmount, setAuctionAmount] = useState(null);

  const [userLobbyData, setUserLobbyData] = useState({ entries: 0, collected: 0 });

  const [auctionButton, setAuctionButton] = useState("Enter Auction")

  const [checkFlag, setCheckFlag] = useState([]);
  const [buttonFlag, setButtonFlag] = useState([false, false]);

  const getLobbyData = async () => {
    if (!contract) return;
    let temp = [];
    for (let i = 0; i < currentDay; i++) {
      const _day = currentDay - i;
      const _availableTokens = lobbyPoolclc(_day);
      const _totalDeposits = await contract.methods.lobbyEntry(_day).call();
      const _userDeposits = await contract.methods.mapMemberLobby(account, _day).call();
      const _dayRate = Number(_totalDeposits) === 0 ? 0 : Number(_availableTokens) / Number(_totalDeposits);
      const _userReceive = Number(_dayRate) * Number(_userDeposits.memberLobbyValue);
      // console.log(_day, _availableTokens, _totalDeposits, _userDeposits.memberLobbyValue, _dayRate, _userReceive);
      temp[i] = {
        day: _day,
        availableTokens: _availableTokens,
        totalDeposits: _totalDeposits,
        userDeposits: _userDeposits.memberLobbyValue,
        dayRate: _dayRate,
        userReceive: _userReceive,
        hasCollected: _userDeposits.hasCollected,
        referrer: _userDeposits.referrer,
      }
    }
    setLobbyData(temp);
  }

  const lobbyPoolclc = (_day) => {
    let starter = 3e6; //- (3e6 * 0.5 / 100)
    let toreturn;

    for (var i = 0; i < _day; i++) {
      let beshown = starter;
      starter -= starter * 5 / 1000;
      toreturn = beshown.toFixed(0);
    }
    return toreturn * 1e18;
  }

  const getValues = async () => {
    if (!contract) return;
    const _overallParticipants = await contract.methods.usersCount().call();
    const _overallBNBDeposits = await contract.methods.overall_lobbyEntry().call();
    const _overallDistributedTokens = await contract.methods.overall_collectedTokens().call();
    const _userLobbyData = await contract.methods.mapMemberLobby_overallData(account).call();
    setOverallParticipants(_overallParticipants);
    setOverallBNBDeposits(Web3.utils.fromWei(_overallBNBDeposits, "ether"));
    setOverallDistributedTokens(Web3.utils.fromWei(_overallDistributedTokens, "ether"));
    // console.log(_userLobbyData);
    setUserLobbyData({ entries: Web3.utils.fromWei(_userLobbyData.overall_lobbyEnteries, "ether"), collected: Web3.utils.fromWei(_userLobbyData.overall_collectedTokens, "ether") })
    setTimeout(() => {
      getValues()
    }, 5000);
  }

  const enterLobbyFinal = async () => {
    if (!account) {
      alert("Wallet is not connected!");
      return;
    }
    if (!contract) return;
    setAuctionButton("... ...");
    try {
      const receipt = await contract.methods.EnterLobby(referrer).send({ from: account, shouldPollResponse: true, value: parseInt(parseFloat(auctionAmount) * 1e18) });
      console.log(receipt);
      alert(`Successfully entered ${auctionAmount} ${MAIN_COIN}`);
      getLobbyData();
    } catch (err) {
      console.log(err);
      alert("Something went wrong!");
    }
    setAuctionButton("Enter Auction");
    setIsWindowOpened(false);
  }

  const collectAuction = async (_day) => {
    if (!account) {
      alert("Wallet is not connected!");
      return;
    }
    if (!contract) return;
    try {
      const receipt = await contract.methods.ExitLobby(_day).send({ from: account, shouldPollResponse: true });
      console.log(receipt);
      alert("Successfully collected!");
      getLobbyData();
    } catch (err) {
      console.log(err);
      alert("Something went wrong!");
    }
  }

  useEffect(() => {
    getValues();
  }, [contract]);

  useEffect(() => {
    getLobbyData();
  }, [contract, currentDay]);

  useEffect(() => {
    if (!balance) return;
    let temp = [];
    temp[0] = auctionAmount <= 0 ? 1 : balance < Number(auctionAmount) ? 2 : 0;
    setCheckFlag(temp);
    setButtonFlag(!temp[0]);
  }, [account, auctionAmount])

  useEffect(() => {
    setPage(1);
  })

  return (
    <div className="lobby">
      <div className="top">
        <div className="info-card-1">
          <div className="title">Overall Participants</div>
          <div className="content">{overallParticipants}</div>
        </div>
        <div className="info-card-2">
          <div className="title">Overall ${MAIN_COIN} Deposits</div>
          <div className="content">{overallBNBDeposits}</div>
        </div>
        <div className="info-card-3">
          <div className="title">Overall distributed ${TOKEN_NAME}</div>
          <div className="content">{overallDistributedTokens}</div>
        </div>
        <Link to="/">
          <div className="title">What is ${PROJECT_NAME}</div>
          <div className="content">Learn more...</div>
        </Link>
      </div>
      <div className="center">
        <div className="left">
          <div className="list" style={{width: '100%', overflow: 'auto', maxHeight: '600px'}}>
            <table>
              <thead>
                <tr>
                  <th>Day</th>
                  <th>Available ${TOKEN_NAME}</th>
                  <th>Total Deposits</th>
                  <th>Your Deposits</th>
                  <th>${TOKEN_NAME} per 1 ${MAIN_COIN}</th>
                  <th>Your Receive</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                {lobbyData.map((data, index) =>
                  <tr key={index}>
                    <td>{data.day}</td>
                    <td>{data.availableTokens / 1e18.toFixed(0)}</td>
                    <td>{data.totalDeposits / 1e18.toFixed(4)}</td>
                    <td>{(data.userDeposits / 1e18).toFixed(4)}</td>
                    <td>{data.dayRate.toFixed(1)}</td>
                    <td>{(data.userReceive / 1e18).toFixed(1)}</td>
                    <td>
                      {
                        data.day == currentDay
                          ? <button className="enter-btn" onClick={() => setIsWindowOpened(true)} >Enter</button>
                          : data.userReceive === 0
                            ? <button className="ended-btn" >Ended</button>
                            : data.hasCollected
                              ? <button className="collected-btn" >Collected</button>
                              : <button className="collect-btn" onClick={() => collectAuction(data.day)} >Collect</button>
                      }
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>
        <div className="right">
          <WalletConnect account={account} setAccount={setAccount} setBalance={setBalance} setTokens={setTokens} />
          <div className="info">
            <div className="title">Balance(${MAIN_COIN})</div>
            <div className="content">{Number(balance).toFixed(4)}</div>
          </div>
          <div className="info">
            <div className="title">Tokens (${TOKEN_NAME})</div>
            <div className="content">{Number(tokens).toFixed(1)}</div>
          </div>
          <div className="info">
            <div className="title">Auction entries (${MAIN_COIN})</div>
            <div className="content">{Number(userLobbyData.entries).toFixed(4)}</div>
          </div>
          <div className="info">
            <div className="title">Collected tokens (${TOKEN_NAME})</div>
            <div className="content">{Number(userLobbyData.collected).toFixed(4)}</div>
          </div>
          <div className="info">
            <div className="title">Referral Bonus (${TOKEN_NAME})</div>
            <div className="content">{(Number(referralBonus)/1e18).toFixed(1)}</div>
          </div>
          <button className="referrer-link" onClick={getReferralLink}>REFERRAL LINK</button>
        </div>
      </div>
      {
        isWindowOpened
          ? <div className="backdrop">
            <div className="auction-input">
              <div className="top">
                <img onClick={() => setIsWindowOpened(false)} src={close} />
              </div>
              <div className="center">
                Amount ${MAIN_COIN} to Enter
                <input value={auctionAmount} onChange={(e) => setAuctionAmount(e.target.value)} placeholder="0.1"></input>
                {
                  checkFlag[0] === 1
                    ? <span>Invaild input data</span>
                    : checkFlag[0] === 2
                      ? <span>No enough balance</span>
                      : null
                }
              </div>
              <div className="bottom">
                <button onClick={enterLobbyFinal} disabled={!buttonFlag}>{auctionButton}</button>
              </div>
            </div>
          </div>
          : null
      }
    </div>
  )
}
